body {
  background-image: url("bckg.8648ff28.jpg");
  background-repeat: repeat;
}

.text {
  text-align: center;
  background-color: #fff;
  border: 5px solid #daa520;
  border-radius: 20px;
  padding: 8px;
  font-family: Comfortaa, sans-serif;
  font-size: 20px;
  font-weight: 500;
}

.main-choice {
  justify-content: center;
  gap: 30px;
  padding-top: 100px;
  display: flex;
}

.main-choice a {
  min-width: 500px;
  height: 333px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-clip: content-box;
  border: 5px solid #daa520;
  border-radius: 20px;
  transition: transform .2s ease-in-out, border-color .2s ease-in-out;
  transform: scale(1);
}

.main-choice a.cat {
  background-image: url("cat.daaa6732.jpg");
}

.main-choice a.dog {
  background-image: url("dog.9def4028.jpg");
}

.main-choice a:hover {
  transform: scale(1.05);
}

.main-choice a.cat:hover {
  border-color: red;
}

.main-choice a.dog:hover {
  border-color: #228b22;
}

.header {
  width: 800px;
  margin: 100px auto 0;
  padding: 20px;
  font-size: 60px;
  font-weight: 700;
}

label {
  margin-top: 40px;
  display: inline-block;
}

.label-select {
  font-family: Comfortaa, sans-serif;
}

.header.choice {
  width: fit-content;
}

.temperament-header {
  font-weight: 700;
}

